@font-face {
  font-family: "New Spirit";
  src: url('/static/fonts/NewSpirit.woff2') format('woff2');
  font-display: swap;
}

@font-face {
  font-family: "Arial Narrow";
  src: url('/static/fonts/arialnarrow-subset.woff2') format('woff2');
  font-display: swap;
}

@font-face {
  font-family: "PT Serif";
  src: url('/static/fonts/pt-serif-v17-latin_cyrillic-regular.woff2') format('woff2');
  font-display: swap;
}

:root {
  /* --ruler-color: aqua; */
  --ruler-color: #74E785;
  --project-background: white;
  --selection-color: #24DC43;
  --hover-color: #6282F0;
  --selection-text-color: var(--font-color);
  --selection-background-color: rgba(36, 220, 67, 0.65);
}

*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  -webkit-tap-highlight-color: transparent;
  overscroll-behavior: none;
  scroll-behavior: auto;
  font-weight: inherit;
}

html {
  height: 100%;
  scroll-behavior: auto;
}

.preloader {
  position: fixed;
  z-index: 9;
  width: 100%;
  height: 100%;
  background: var(--font-color);
}

body {
  height: 100%;
  font-weight: 100;
  letter-spacing: -0.2pt;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  background: var(--back-color);
  overflow-x: clip;
  scroll-behavior: auto;
}

/* @media screen and (max-width: 1300px) and (pointer: coarse) {
    body {
        -webkit-font-smoothing: antialiased;
        overflow-x: hidden;
    }
} */

::selection {
  color: var(--selection-text-color);
  background-color: var(--selection-background-color);
}

.background__rect {
  position: fixed;
  width: 100%;
  z-index: 0;
}

.background {
  z-index: 0;
}

#f {
  height: 23.6vh;
  background: var(--f-color);
}

/* #s {
  height: 100%;
  background: var(--back-color);
} */

#t {
  bottom: 0 !important;
  height: 23.6vh;
  background: var(--t-color);
}

.header {
  position: sticky;
  z-index: 1;
  margin: auto;
  width: 82vw;
  height: 14.6vh;
  font-family: "Arial Narrow", sans-serif-condensed, sans-serif;
  font-weight: 400;
  font-size: 24px;
  color: var(--font-color);
}

.header__menu {
  display: flex;
  flex-wrap: wrap;
  column-gap: 3.1vh;
  row-gap: 1vh;
  padding-top: calc(5.6vh + (24px * -0.4));
  width: 100%;
}

.header__title,
.current-city,
.mytime {
  display: inline-block;
  font-family: inherit;
  font-weight: inherit;
  font-size: inherit;
}

.mytime {
  min-width: 75px;
}

.header__list {
  display: flex;
  gap: 20px;
}

.list__item {
  list-style: none;
}

.item__link {
  color: inherit;
  z-index: 0;
}

.link__icon {
  fill: currentColor;
}

.content {
  font: 48px 'New Spirit', serif;
  position: absolute;
  left: 9vw;
  top: 14.6vh;
  display: block;
  width: 82vw;
}

@media screen and (max-width: 1024px) {
  .content {
    font: 36px 'New Spirit', serif;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  margin: 0;
  padding: 0;
  width: 1px;
  height: 0;
  border: 0;
  white-space: nowrap;
}

.webgl {
  width: 100% !important;
  height: auto !important;
}

.about {
  height: auto;
  margin-bottom: 5.6vh;
  max-width: 100%;
}

#intro {
  max-width: 100%;
  color: var(--font-color);
  --module: 9vh;
  --fontSize: 48px;
  --lineHeight: calc(var(--fontSize) + (var(--module) - ((var(--fontSize) - (var(--fontSize) * var(--FONT_TOP_RATIO))) + (var(--fontSize) - (var(--fontSize) * var(--FONT_BOTTOM_RATIO))))));
  --FONT_XHEIGHT_OFFSET: calc(var(--fontSize) * var(--FONT_TOP_RATIO));
  --xheightOffset: calc(((var(--lineHeight) - var(--fontSize)) / 2) + var(--FONT_XHEIGHT_OFFSET));
  /* --xheightOffset: calc(max(var(--FONT_XHEIGHT_OFFSET),
                (max(var(--fontSize), var(--lineHeight)) - min(var(--fontSize), var(--lineHeight))) / 2) - min(var(--FONT_XHEIGHT_OFFSET),
                (max(var(--fontSize), var(--lineHeight)) - min(var(--fontSize), var(--lineHeight))) / 2)); */
  --fontBottomOffset: calc(var(--fontSize) * var(--FONT_BOTTOM_RATIO));
  line-height: var(--lineHeight);
  margin-top: calc(var(--xheightOffset) * -1);
  margin-bottom: calc(var(--fontBottomOffset) * -1);
}

#intro span {
  color: #24DC43;
}

@media screen and (max-width: 1024px) {
  #intro {
    max-width: 100%;
    color: var(--font-color);
    --fontSize: 36px;
    /* --module: 9vh; */
    /* --FONT_XHEIGHT_OFFSET: calc(var(--fontSize) * var(--FONT_TOP_RATIO)); */
    --lineHeight: calc(var(--fontSize) + ((var(--module) - (var(--fontSize) * 2)) / 2));
    --xheightOffset: calc(((var(--lineHeight) - var(--fontSize)) / 2) + var(--FONT_XHEIGHT_OFFSET));
    /* --fontBottomOffset: calc(var(--fontSize) * var(--FONT_BOTTOM_RATIO)); */
    line-height: var(--lineHeight);
  }
}

@media screen and (max-height: 600px) {
  #intro {
    max-width: 100%;
    color: var(--font-color);
    --fontSize: 36px;
    --lineHeight: calc(var(--module));
    --xheightOffset: calc(var(--fontSize) * var(--FONT_TOP_RATIO) + (var(--lineHeight) - var(--fontSize)) / 2);
    line-height: max(24px, var(--lineHeight));
  }
}

