.display {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr !important;
  column-gap: 2.1vw;
  margin-bottom: 9vh;
  margin-left: calc((9vw - 5.6vw) * -1);
  margin-right: calc((9vw - 5.6vw) * -1);
  z-index: -1;
}

@media screen and (max-width: 1024px) {
  .display {
    margin-bottom: 5.6vh;
    grid-template-columns: 1fr !important;
    row-gap: 2.1vh;
  }
}

/* @media screen and (1024: 640px) and (orientation: landscape) {
    .display {
        column-gap: 2.1vw;
        grid-template-columns: 1fr 1fr 1fr !important;
        margin-bottom: 9vh;
    }
} */

.display__project {
  height: 52.8vh !important;
  width: 100% !important;
  background-color: hsl(0, 0%, 95%);
  cursor: grab;
}

.blurred {
  /* -webkit-filter: blur(8px); */
  filter: blur(8px);
  /* -webkit-transition: -webkit-filter 3s; */
  transition: filter 3s;
  /* will-change: -webkit-filter; */
  will-change: filter;
}

/* .blurred:hover {
    -webkit-filter: blur(2px);
    -webkit-transition: -webkit-filter 3s;
} */

.preloader {
  cursor: wait;
  height: 100% !important;
  width: 100% !important;
}

.links {
  display: grid;
  row-gap: 5.6vh;
  margin-bottom: 5.6vh;
}

h3 {
  margin-block-start: 0 !important;
  margin-block-end: 0 !important;
}

.links__link {
  display: block;
  width: fit-content !important;
  height: fit-content;
  /* margin-bottom: 5.6vh;
    font-weight: inherit;
    font-size: inherit;
    line-height: 100%; */
}

.links__link a {
  z-index: 0;
  display: block;
  width: fit-content !important;
  --fontSize: 48px;
  --fontTopOffset: calc((var(--fontSize) * var(--FONT_TOP_RATIO)) * var(--LINE_HEIGHT_RATIO));
  --fontBottomOffset: calc((var(--fontSize) * var(--FONT_BOTTOM_RATIO) * var(--LINE_HEIGHT_RATIO)));
  margin-top: calc(var(--fontTopOffset) * -1);
  margin-bottom: calc(var(--fontBottomOffset) * -1);
  font-size: inherit;
  text-align: left;
  text-decoration-color: var(--font-color);
  text-underline-offset: calc(5.6vh - 3.7vh);
  text-decoration-thickness: 1px;
  color: var(--font-color);
  outline: none;
  transition: color 0.1s linear;
  transition: text-decoration 0.01s linear;
  line-height: 1;
}

@media screen and (max-width: 1024px) {
  .links { row-gap: 6.45vh; }

  .links__link a {
    color: var(--font-color);
    --fontSize: 36px;
    line-height: 1.3;
    --fontTopOffset: calc((var(--fontSize) * var(--FONT_TOP_RATIO) * var(--LINE_HEIGHT_RATIO)));
    --fontBottomOffset: calc((var(--fontSize) * var(--FONT_BOTTOM_RATIO) * var(--LINE_HEIGHT_RATIO)));
    margin-top: calc(var(--fontTopOffset) * -1 - ((36px * var(--LINE_HEIGHT_RATIO)) * 1.3 - (36px * var(--LINE_HEIGHT_RATIO))) / 2);
    margin-bottom: calc(var(--fontBottomOffset) * -1 - ((36px * var(--LINE_HEIGHT_RATIO)) * 1.3 - (36px * var(--LINE_HEIGHT_RATIO))) / 2);
  }
}

.item__link:focus-visible,
.links__link:focus-visible,
h3:focus-visible,
a:hover,
a:focus-visible {
  outline: none;
  z-index: 4;
  /* text-decoration: none; */
  transition: color 0.01s linear;
  transition: text-decoration 0.01s linear;
  animation: blink 0.5s infinite;
}

@keyframes blink {

  0%,
  49%,
  to {
    opacity: 1
  }

  50%,
  99% {
    opacity: 0
  }
}

#resize-container {
  position: absolute;
  top: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
  scroll-behavior: auto;
}

#resize-container.show {
  display: block;
  scroll-behavior: auto;
}

.toggle-grid {
  display: block;
  cursor: help;
}

#resize-value {
  font: 48px 'New Spirit', serif;
  position: fixed;
  --fontSize: 48px;
  bottom: calc(5.6vh - (var(--fontSize) * var(--FONT_BOTTOM_RATIO)));
  right: 3.4vw;
  z-index: 3;
  display: block;
  text-align: right;
  line-height: 1;
  color: var(--font-color);
}

@media screen and (max-width: 1024px) {
  #resize-value {
    color: var(--font-color);
    font: 36px 'New Spirit', serif;
    --fontSize: 36px;
    --fontBottomOffset: calc((var(--fontSize) * var(--FONT_BOTTOM_RATIO)));
    line-height: 1;
    margin-bottom: calc(var(--fontBottomOffset) * -1);
    bottom: 5.6vh;
  }
}

#resize-value-hide {
  display: none;
}

.hover-reveal {
  position: fixed;
  z-index: 1 !important;
  top: 0;
  left: 0;
  width: 270px;
  height: 540px;
  pointer-events: none;
  opacity: 0;
}

.hover-reveal__inner {
  overflow: hidden;
  z-index: 1 !important;
}

.hover-reveal__inner,
.hover-reveal__img {
  border-radius: 10px;
  position: relative;
  z-index: 1 !important;
  width: 100%;
  height: 100%;
}

.hover-reveal__img {
  background-size: cover;
  background-position: 50% 50%;
}

